// Variables
@import "variables";

.panel-heading {  display: flex;  justify-content: space-between;  align-items: center;  }
.filter {  display: flex;  justify-content: space-between;  align-items: center;  margin-bottom: 15px;
  &-column {  flex: 4;  padding-right: 15px;  }
  &-operator {  flex: 4;  padding-right: 15px;  }
  &-input {  flex: 6;  padding-right: 15px;  }
  &-btn {  flex: 1;  }
}
.pagination-footer {  display: flex;  justify-content: space-between;  align-items: center;  }
.pagination-input {  width: 45px;  }


.img-user{  max-width: 60px;  max-height: 60px;  }
.img-edit{  max-width: 100px;  max-height: 150px;  }
.mt {margin-top: 10px !important;}
.img-field{padding: 5px !important; max-height: 100px}
.img-field-small{min-width: 60px;min-height: 60px;max-width: 60px;max-height: 60px}
.pe-blue{color: #2360A5; font-size: 18px}
.hr-blue{margin-top: 6px;margin-bottom: 6px;border-top: 1px solid  #2360A5;}
.category-blue{color:  #2360A5 !important;}
.spanLink {color: #2360A5 !important;}
.spanLink:hover {color: #2d8ff6 !important;font-weight: bold}
